import React, { useState, useEffect } from "react";

function AddToCartButton({ item }) {
  const [isInCart, setIsInCart] = useState(false);
  const [displayText, setDisplayText] = useState("");

  useEffect(() => {
    const cart = JSON.parse(localStorage.getItem("cart")) || [];
    const itemInCart = cart.some(
      (cartItem) => cartItem.gameUrl === item.gameUrl
    );
    setIsInCart(itemInCart);
    setDisplayText(itemInCart ? " " : "  ");
  }, [item.gameUrl]);

  const handleCartAction = () => {
    if (isInCart) {
      //     ,    
      window.location.href = "#/cart";
      return;
    }

    //    
    const newState = true;
    setIsInCart(newState);
    setDisplayText(" ");

    //      
    const currentPath = window.location.hash.replace(/^#/, "") || "/";
    const cleanUrl = `${currentPath}`; //  URL  

    let cart = JSON.parse(localStorage.getItem("cart")) || [];
    const cartItem = {
      title: item.title,
      url: cleanUrl, //   URL
      gameUrl: item.gameUrl, //     PS Store
      cover: item.cover,
      price: item.convertedPrice || item.price_rub || " ",
      edition: item.edition || "",
      platforms: item.platforms,
      type: item.type || "game",
    };
    cart.push(cartItem);
    localStorage.setItem("cart", JSON.stringify(cart));
  };

  return (
    <div className="button-wrapper">
      <button
        onClick={handleCartAction}
        className={`button button--add-to-cart ${
          isInCart ? "button--in-cart" : "button--add-to-cart"
        }`}
      >
        <span className="button-text" key={displayText}>
          {displayText}
        </span>
        {isInCart && <div className="arrow"></div>}
      </button>
    </div>
  );
}

export default AddToCartButton;
